body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  /* background-image: linear-gradient(155deg , #0a5aa5 5%, #000000 30% ,black 60%); */
  background-image: linear-gradient(155deg , #155794 10% , #000000 90% ,#ff8717 10%);
  background-color: black;
  background-repeat: no-repeat;
  min-height: 100vh;
  color: white;
  font-family: Berlin Sans FB;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

::-webkit-scrollbar {
  width: 9px;
}
/* Track */
::-webkit-scrollbar-track {
  background: black; 
}
/* Handle */
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: #d4d4d4; 
}
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: white; 
  box-shadow:  0 0 5px black; 
  border-radius: 10px;
}


.full{
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;

}
.header,.works,.footer{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
.header,.footer{
  font-size: 20px;
  height: 25vh;
  margin-top: 5vh;
}
.header{
  transform: translateY(-50%);
}
.works{
  min-height: 50vh;
}
.me{
  width: 90px;
  height: 90px;
  border-radius: 10px;
  background-color: white;
}
.skilles{
  color: #cccccc;
}
.work-img{
  width: 70px;
  height: 70px;
  border-radius: 12px;
  box-shadow: 0 0 8px #0a5aa5;
}
.tool{
  color: #9b9b9b;
  font-style: italic;
}
.list{
  width: fit-content;
  padding: 1px;
  display: grid;
  grid-template-columns: 33.3% 33.3% 33.3%;
  align-content: center;
  justify-items: center;
  
  background-color: rgba(0, 255, 255, 0);
}
.item{
  margin: 20px;
  display: grid;
  justify-items: center;
  gap: 5px;
  text-transform: none;
  text-decoration: none;
  color: white;

}
.links{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 200px;
  
}
.link{
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.link img{
  position: absolute;
  background-color: rgba(0, 0, 0, 0);
  width: 28px;
  height: 28px;
  border-radius: 3px;
}

.side{
  width: 28px;
  height: 28px;
  background-color:  #0a5aa500;
  z-index: -1;
  position: absolute;
  border-radius: 3px;
}
.tit{
  color: #fa973a;
  font-size: 20px;
}
.footer:first-child{
  color: red;
}
.water{
  position: absolute;
  right: 20px;
  bottom: 20px;
  transform: rotate(-25deg);
}
@media screen and (max-width: 768px) {
  .list{
    grid-template-columns: 100%;
  }
}
@media screen and (max-width: 769px) and (min-width: 992px) {
  .list{
    /* width: 200px; */
    grid-template-columns: 50% 50%;
  }
}
@media screen and (min-width: 993px) {
  .list{
    /* width: 200px; */
    grid-template-columns: 25% 25% 25% 25%;
  }
}

.null{
  transform: translateY(-50%);
  transform: translateY(-50%);
  color:  #2369aa;
}